import * as React from 'react';
import { useLocation } from 'react-router-dom';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { Wrapper, Status } from '@googlemaps/react-wrapper';
import Loader from '../components/Loader';

const render = (status: Status): React.ReactElement | null => {
  return (
    <>
      <Snackbar
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        open={status === Status.FAILURE}
      >
        <MuiAlert
          elevation={6}
          variant="filled"
          severity="error"
          sx={{ width: '100%' }}
        >
          {'Error loading maps!'}
        </MuiAlert>
      </Snackbar>
      <Loader visible={status === Status.LOADING} />
    </>
  );
};

export default function GeneralMap() {
  const center = { lat: 39.828922654821866, lng: -98.57947970000001 };
  const zoom = 5;
  const location = useLocation();
  return (
    <>
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 1, height: '100%', width: '100%' }}
      >
        <Wrapper
          apiKey={'AIzaSyCXHo65x6xwfIPmz5GAZ2B2laVghupH7Dg'}
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          render={render}
        >
          <Map center={center} zoom={zoom} />
        </Wrapper>
      </Box>
    </>
  );
}

function Map({
  center,
  zoom,
  children,
}: {
  center: google.maps.LatLngLiteral;
  zoom: number;
  children?: React.ReactNode;
}) {
  const mapRef = React.useRef(null);
  const [map, setMap] = React.useState<google.maps.Map>();

  React.useEffect(() => {
    if (mapRef.current && !map) {
      new window.google.maps.Map(mapRef.current, {
        center,
        zoom,
      });
    }
  }, [mapRef, map]);
  return (
    <>
      <div style={{ height: '100%', width: '100%' }} ref={mapRef} id="map" />
      {children}
    </>
  );
}

const Marker: React.FC<google.maps.MarkerOptions> = (options) => {
  const [marker, setMarker] = React.useState<google.maps.Marker>();

  React.useEffect(() => {
    if (!marker) {
      setMarker(new window.google.maps.Marker());
    }

    return () => {
      if (marker) {
        marker.setMap(null);
      }
    };
  }, [marker]);

  React.useEffect(() => {
    if (marker) {
      marker.setOptions(options);
    }
  }, [marker, options]);

  return null;
};
